/**
 * 动作构件元数据
 */
export class ActionComponent extends AbstractComponent{
    /**是否支持补偿 */
    Compensable:boolean;

    ServiceUnit:string;

    
    // /**
    //  * 序列化动作构件
    //  */
    // serialize(): Object{
    
    // }

    /**
    * 初始化动作构件
     * @param obj 
     */
    init(obj: Object): void{
        super.init(obj);
        this.Compensable=obj["Compensable"];
        this.ServiceUnit=obj["ServiceUnit"];
    }

    public getEmptyMethodInstance(): AbstractComponentMethod {
        return new ActionMethod();
    }
}

/**
 * 动作构件方法
 */
export class ActionMethod extends AbstractComponentMethod{
    public getEmptyParamterInstance(): AbstractComponentParameter {
        return new ActionParameter();
    }
}

/**
 * 动作构件参数
 */
export class ActionParameter extends AbstractComponentParameter{
}


