import { Component, Input, OnChanges, ViewChild, OnInit, EventEmitter } from '@angular/core';
import { TypeObject, typeList } from '../commonobject/commonobject';
import { isNullOrUndefined } from 'util';
import { CommonModelRefer } from '@gsp-cmp/component';
import { NewModelModalComponent } from './modal/modelmodal.component';
import { DialogComponent, DialogService, DialogCloseResult, DialogAction } from '@progress/kendo-angular-dialog'
import { SelectAllCheckboxState } from '@progress/kendo-angular-grid'
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils'
import { ActionComponent } from '../ccmpmetadata/ccmpmetadata';

@Component({
  selector: 'app-ccmpbaseinfo',
  templateUrl: './ccmpbaseinfo.component.html'
})

export class CmpBaseInfoComponent implements OnInit {
  @Input() cmpmetadata: ActionComponent;
  @ViewChild('modelDialog') modelDialog: DialogComponent;
  modelType: TypeObject[] = [{ text: "CM", value: 0 }];
  newModel: CommonModelRefer;
  selectedModelList: CommonModelRefer[];
  selectedIds: any[];
  selectAllState: SelectAllCheckboxState = 'unchecked';
  status: Array<string> = ["是","否"];
  constructor(private dialogService: DialogService) { }

  ngOnInit(): void {
    this.newModel = {} as CommonModelRefer;
    this.selectedModelList = [] as CommonModelRefer[];
    this.selectedIds = [];
  }

  addComponentModel() {
    this.newModel = {} as CommonModelRefer;
    const dialogRef = this.dialogService.open({
      title: '新增模型',
      content: NewModelModalComponent,
      actions: [
        { text: '关闭' },
        { text: '确认', primary: true }
      ],
      width: 400,
      height: 280
    });
    const intance = dialogRef.content.instance;
    intance.newModel = this.newModel;
    const actionEmitter = dialogRef.dialog.instance.action;
    dialogRef.dialog.instance.action = new EventEmitter<any>();
    const sub = dialogRef.dialog.instance.action.subscribe(action => {
      if (action.text == "确认") {
        if (!this.modelReferenceEmptyCheck(this.newModel)) {
          actionEmitter.emit(action);
        }
      }
      else {
        actionEmitter.emit(action);
      }
    });
    dialogRef.result.subscribe((dialogResult) => {
      //;
      sub.unsubscribe();
      if (!(dialogResult instanceof DialogCloseResult)) {
        let actionName = (dialogResult as DialogAction).text;
        if (actionName == "确认") {
          //;
          this.newModel = dialogRef.content.instance.newModel;
          this.getModelList(this.newModel);
        }
      }
    });
  }

  getModelList(newModel: CommonModelRefer) {
    if (!this.cmpmetadata.Models) {
      this.cmpmetadata.Models = [];
    }
    if (this.cmpmetadata.Models.length <= 0) {
      this.cmpmetadata.Models.push(this.newModel);
    }
    else {
      let tempIndex = this.cmpmetadata.Models.findIndex(value => value.ModelId == this.newModel.ModelId);
      if (tempIndex != (-1)) {
        alert("已经存在模型ID为" + this.newModel.ModelId + "的通用模型");
        return;
      }
      else {
        let tempIndex = this.cmpmetadata.Models.findIndex(value => value.ModelCode == this.newModel.ModelCode);
        if (tempIndex != (-1)) {
          alert("模型编号" + this.newModel.ModelCode + "重复");
          return;
        }
        this.cmpmetadata.Models.push(this.newModel);
      }
    }
  }

  modelReferenceEmptyCheck(newModel: CommonModelRefer): boolean {
    if (isNullOrEmptyString(newModel.ModelId) || isNullOrUndefined(newModel.ModelId)) {
      alert("模型ID不能为空");
      return true;
    }
    if (isNullOrEmptyString(newModel.ModelCode) || isNullOrUndefined(newModel.ModelCode)) {
      alert("模型编号不能为空");
      return true;
    }
    if (isNullOrEmptyString(newModel.ModelName) || isNullOrUndefined(newModel.ModelName)) {
      alert("模型名称不能为空");
      return true;
    }
    return false;
  }

  //获取选择项事件
  onSelectedKeysModelChange(e) {
    this.selectedModelList = [] as CommonModelRefer[];
    let len = this.selectedIds.length;
    if (len > 0) {
      this.selectedIds.forEach(id => {
        if (this.cmpmetadata.Models && this.cmpmetadata.Models.length > 0) {
          let tempIndex = this.cmpmetadata.Models.findIndex(value => value.ModelId == id);
          if (tempIndex != (-1)) {
            this.selectedModelList.push(this.cmpmetadata.Models[tempIndex]);
          }
        }
      });
    }
    if (len === 0) {
      this.selectAllState = 'unchecked';
    } else if (len > 0 && len < this.cmpmetadata.Models.length) {
      this.selectAllState = 'indeterminate';
    } else {
      this.selectAllState = 'checked';
    }
  }

  //全选框事件
  onSelectAllModelChange(checkedState: SelectAllCheckboxState) {
    if (checkedState === 'checked') {
      this.selectedIds = this.cmpmetadata.Models.map((item) => item.ModelId);
      this.selectAllState = 'checked';
    } else {
      this.selectedIds = [];
      this.selectAllState = 'unchecked';
    }
  }

  deleteComponentModelList() {
      if (!this.selectedModelList || this.selectedModelList.length <= 0) {
        alert('请选择需要删除的模型。');
        return;
      }
      this.selectedModelList.forEach(element => {

        this.deleteSpecifiedElement(this.cmpmetadata.Models, element);
        this.deleteSpecifiedElement(this.selectedIds, element.ModelId);
      });
      this.selectedModelList = [];
      this.selectAllState = "unchecked";
    }

    //删除指定元素
    deleteSpecifiedElement(inputArray: Array<any>, element: any) {
      let tempIndex = inputArray.findIndex(value => value == element);
      if (tempIndex != (-1)) {
        inputArray.splice(tempIndex, 1);
      }
  }

  compensable(){
    return this.cmpmetadata?(this.cmpmetadata.Compensable?"是":"否"):"否"
  }

  compensableChanged(value:string){
    this.cmpmetadata.Compensable= value=="是"?true:false;
  }


  
 

}


